.BuildControls__BuildControls__220uz{
    width: 100%;
    background-color:#CF8F2E;
    display:flex;
    flex-flow:column;
    align-content: center;
    box-shadow: 0 2px 1px #ccc;
    margin:auto;
    padding: 5px 0;
}

.BuildControls__BuildControls__220uz p{
    text-align: center;
}

.BuildControls__OrderButton__1ScSk {
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #712309;
    font-family: inherit;
    font-size: 1.2em;
    padding: 0px;
    text-align: center;
    box-shadow: 2px 2px 2px #712309;
    width: 270px;
    display: block;
    height: 50px;
    margin: 10px auto;
    align-content: center
}

.BuildControls__OrderButton__1ScSk:hover, .BuildControls__OrderButton__1ScSk:active {
    background-color: #DAD735;
    border: 1px solid #966909;
    color: #966909;
}

.BuildControls__OrderButton__1ScSk:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
    box-shadow: 2px 2px 2px #888888;
}

.BuildControls__OrderButton__1ScSk:not(:disabled) {
    -webkit-animation: BuildControls__enable__3XAWA 0.3s linear;
            animation: BuildControls__enable__3XAWA 0.3s linear;
}

@-webkit-keyframes BuildControls__enable__3XAWA {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes BuildControls__enable__3XAWA {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}